@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .reasons {
        &_block {
            align-items: flex-start; }
        &_descr {
            width: 300px; } }
    .mobile_item {
        width: 300px; }
    .choice {
        padding: 55px 0;
        min-height: 840px; }
    .footer {
        &_wrapper  >div {
            width: 50%;
            &:nth-child(1) {
                width: 25%; }
            &:nth-child(3) {
                width: 25%;
                display: flex;
                justify-content: flex-end; } }
        &_mobile {
            display: flex;
            a {
                margin: 0 3px;
                img {
                    width: 100%; } } } } }
@media (max-width: 991px) {
    .menu {
        &_item {
            padding-right: 8px; }
        &_link {
            font-size: 10px; } }
    .subheader {
        &_logo {
            img {
                width: 100%; } }
        &_official {
            font-size: 11px; }
        &_callnumber {
            font-size: 18px; }
        &_btn {
            width: 100%;
            height: 40px;
            margin-top: 21px; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 30px; } }
    .reasons {
        &_block {
            align-items: center;
            flex-direction: column; }
        &_round {
            margin-right: 0; }
        &_descr {
            width: 300px;
            margin-top: 20px; } }
    .mobile_item {
        width: 225px;
        height: 225px;
        &_subtitle {
            font-size: 17px;
            transform: translateY(-23px); } }
    .choice {
        min-height: 800px;
        img {
            width: 100%; } }
    .require {
        padding-bottom: 40px;
        min-height: 1050px;
        &_title {
            margin-bottom: 30px; }
        &_block {
            margin-top: 0; } }

    .world {
        min-height: 650px; }
    .footer {
        &_wrapper {
            flex-wrap: wrap; }
        &_wrapper > div {
            width: 100%;
            display: flex;
            justify-content: center;
            margin-bottom: 20px;
            &:nth-child(1) {
                width: 100%; }
            &:nth-child(3) {
                width: 100%;
                justify-content: center; } } } }
@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0,0,.8);
        transition: 0.6s all;
        &_active {
            left: 0; }
        &_item {
            padding-right: 0;
            border: none;
            margin-top: 10px 0; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_callnumber {
            display: block;
            color: #ffffff;
            font-size: 16px;
            font-weight: 400;
            text-transform: uppercase;
            margin-top: 12px;
            text-align: center; }
        &_btn {
            display: block;
            margin: 0 auto;
            margin-top: 5px;
            width: 145px;
            height: 30px; } }
    .promo {
        &_header {
            line-height: 45px; } }
    .reasons {
        padding: 40px 0;
        &_descr {
            width: 350px; }
        &_subtitle {
            text-align: center; }
        &_text {
            margin-top: 10px; } }
    .mobile {
        padding: 40px 0;
        width: 100%;
        display: flex;
        &_item {
            width: 45%; } }
    .apportunities {
        img {
            width: 100%; } }
    .require {
        padding-top: 0;
        min-height: 1325px; } }
@media (max-width: 575px) {
    .menu {
        width: 85%;
        &_item {
            width: 100%;
            text-align: center;
            padding: 5px 0; }
        &_link {
            display: block; } }
    .title {
        font-size: 14px; }
    .subheader {
        height: 80px;
        &_official {
            display: none; }
        &_logo {
            margin-top: 29px; }
        &_callnumber {
            color: #ffffff;
            font-size: 12px;
            text-transform: uppercase;
            margin-top: 17px; }
        &_btn {
            width: 110px;
            height: 22px;
            font-size: 8px;
            margin-top: 9px;
            padding: 0; } }

    .promo {
        min-height: 400px;
        padding: 102px 0 35px 0;
        &_header {
            font-size: 30px; }
        &_subheader {
            margin-top: 34px;
            font-size: 20px; }
        &_text {
            display: none; }
        &_btn {
            width: 210px;
            height: 50px;
            margin: 75px auto 0 auto; } }
    .reasons {
        padding: 20px 0 82px 0;
        .title {
            margin-top: 18px;
            font-size: 16px; }
        &_text {
            text-align: justify; }
        &_round {
            margin-right: 0; }
        &_descr {
            width: 100%; } }
    .mobile {
        padding: 25px 0 43px 0;
        background: url(../img/mobile_bg.jpg) center center/cover no-repeat;
        .row {}
        &_item {
            width: 290px;
            height: 254px;
            padding: 81px 40px 0 40px;
            &_subtitle {
                font-size: 18px; } }
        .title {
            margin-top: 16px;
            font-size: 16px; }
        .subtitle {
            margin-top: 9px;
            font-size: 14px; } }

    .apportunities {
        padding: 27px 0 31px 0;
        min-height: 393px;
        background: #070716;
        &_title {
            display: none; }
        &_subtitle {
            display: none; }
        img {
            display: block;
            width: 100%;
            margin: 36px auto 21px auto; }
        &_text {
            color: #FFFFFF; } }
    .require {
        margin-top: 7px;
        min-height: 955px;
        padding: 20px;
        &_title {
            font-size: 16px;
            margin-top: 16px;
            margin-bottom: 0; }
        &_subtitle {
            font-size: 16px; }
        &_list {
            padding-left: 30px;
            li {
                font-size: 14px;
                line-height: 16px; } }
        &_block {
            margin: 30px 0 0 0; }
        &_descr {
            font-size: 14px; } }
    .world {
        min-height: 360px;
        margin-top: 30px;
        padding: 26px 0 30px 0;
        &_link {
            width: 210px;
            bottom: 30px;
            display: block;
            text-align: center; }
        &_title {
            font-size: 14px;
            margin-top: 16px; }
        &_subtitle {
            font-size: 14px;
            margin-top: 11px; } }
    footer {
        padding: 25px 0 20px 0; }
    .footer {
        &_line {
            width: 255px;
            margin: 0 auto; }
        &_wrapper {
            padding-top: 22px;
            flex-direction: column;
            align-items: center; }
        &_wrapper > div {
            margin-bottom: 20px;
            width: 100%;
            &:nth-child(1) {
                order: 2; }
            &:nth-child(2) {
                order: 1; }
            &:nth-child(3) {
                order: 3; } }
        &_links {
            width: 100%;
            &_main {
                padding: 0;
                a {
                    font-size: 14px; } }
            &_sub {
                flex-wrap: wrap;
                justify-content: center;
                a {
                    margin: 0 7px; } }
            &_lang {
                margin-top: 10px; } }
        &_social {
            width: 100%;
            display: flex;
            justify-content: center; } } }
