a {
    text-decoration: none; }

header {
    position: relative;
    height: 50px; }
nav {
    position: fixed;
    width: 100%;
    height: 50px;
    background-color: #212121;
    z-index: 5; }
.hamburger {
    display: none; }
.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    &_link {
        font-weight: 300;
        font-size: 15px;
        color: #FFFFFF;
        &:hover {
            color: $main-color; } }
    &_item {
        border-right: 2px dotted black;
        padding-right: 28px;
        &:last-child {
            border-right: none; } } }
.subheader {
    position: absolute;
    top: 40px;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    &_logo {
        display: block;
        margin-top: 17px;
        max-width: 100%; }
    &_official {
        margin-top: 6px;
        opacity: 0.5;
        font-weight: 400;
        font-size: 13px;
        color: #FFFFFF; }
    &_call {
        margin-top: 17px;
        font-weight: 300;
        font-size: 12px;
        text-align: right;
        color: #FFFFFF; }
    &_callnumber {
        margin-top: 8px;
        display: block;
        font-weight: 400;
        font-size: 22px;
        text-align: right;
        text-transform: uppercase;
        color: #FFFFFF; }
    &_btn {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        font-weight: 400;
        font-size: 14px;
        // display: flex
        // align-items: center
        // text-align: center
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        transition: 0.5s;
        @include btn_hover; } }
.promo {
    margin: 0;
    min-height: 800px;
    padding: 150px 0 135px 0;
    background: url("../img/bg_1.jpg") center (center / cover) no-repeat;
    &_header {
        font-weight: 700;
        font-size: 60px;
        text-transform: uppercase;
        color: #FFFFFF;
        text-align: center; }
    &_subheader {
        font-weight: 700;
        font-size: 36px;
        text-transform: uppercase;
        color: #FFFFFF;
        text-align: center; }
    &_text {
        font-weight: 400;
        font-size: 18px;
        text-align: center;
        color: #FFFFFF; }
    &_btn {
        display: block;
        margin: 160px auto 0 auto;
        width: 172px;
        height: 46px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        border: none;
        font-weight: 400;
        font-size: 14px;
        // display: flex
        // align-items: center
        // text-align: center
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        transition: 0.5s;
        @include btn_hover; } }
.reasons {
    padding: 71px 0 65px 0;
    .label {
        position: relative;
        display: block;
        margin: 0 auto;
        height: 24px;
        width: 112px;
        padding: 5px 12px;
        background-color: #000000;
        color: #ffffff;
        font-size: 11px;
        font-weight: 300;
        text-transform: uppercase;
        text-align: center;
        line-height: initial;
        z-index: 2; }
    .title {
        position: relative;
        margin-bottom: 0;
        margin-top: 38px;
        font-weight: 700;
        font-size: 38px;
        text-transform: uppercase;
        color: #222222;
        text-align: center;
        z-index: 2; }
    .subtitle {
        position: relative;
        margin-top: 19px;
        color: #222;
        font-style: 17px;
        font-weight: 400;
        text-align: center;
        z-index: 2; }
    &_block {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 35px; }
    &_round {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 26px;
        width: 113px;
        height: 113px;
        background-color: $main-color;
        border-radius: 100%; }
    &_descr {
        width: 343px; }
    &_subtitle {
        line-height: 21px;
        color: $main-color;
        font-size: 17px;
        font-weight: 400; }
    &_text {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px; } }
.mobile {
    min-height: 864px;
    padding: 79px 0 80px 0;
    background: url('../img/mobile_bg.jpg') center (center/cover) no-repeate;
    .label {
        margin: 0 auto;
        width: 112px;
        height: 24px;
        padding: 5px 12px;
        background-color: #000000;
        color: #ffffff;
        font-size: 11px;
        font-weight: 300;
        text-transform: uppercase;
        text-align: center;
        line-height: initial; }
    .title {
        margin: 38px 0 0 0;
        font-weight: 700;
        font-size: 38px;
        text-transform: uppercase;
        color: #222222;
        text-align: center; }
    .subtitle {
        margin-bottom: 30px;
        font-weight: 400;
        font-size: 17px;
        color: #222222;
        text-align: center; }
    &_item {
        position: relative;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 91px;
        width: 320px;
        height: 280px;
        opacity: 0.9;
        &_1 {
            background: url('../img/card1.jpg') center (center / cover) no-repeat; }
        &_2 {
            background: url('../img/card2.jpg') center (center / cover) no-repeat; }
        &_3 {
            background: url('../img/card3.jpg') center (center / cover) no-repeat; }
        &_4 {
            background: url('../img/card4.jpg') center (center / cover) no-repeat; }
        &_5 {
            background: url('../img/card5.jpg') center (center / cover) no-repeat; }
        &_6 {
            background: url('../img/card6.jpg') center (center / cover) no-repeat; }
        &_subtitle {
            font-weight: 400;
            font-size: 24px;
            text-transform: uppercase;
            text-align: center;
            color: #ffffff; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 29px;
            height: 29px;
            border-radius: 100%;
            background: #000000;
            margin: 0 auto;
            padding: 6px;
            cursor: pointer;
            &::before {
                content: '';
                display: block;
                width: 14px;
                height: 2px;
                background-color: $main_color; }
            &::after {
                content: '';
                display: block;
                height: 14px;
                width: 2px;
                background-color: $main_color;
                position: absolute;
                transition: 0.3s all; }
            &:hover {
                &:before, &::after {
                    background-color: $second_color; } } } } }
.apportunities {
    min-height: 897px;
    padding: 79px 0 80px 0;
    background: url('../img/bg_for_car.png') center (center/cover) no-repeat;
    &_label {
        margin: 0 auto;
        width: 112px;
        height: 24px;
        padding: 5px 12px;
        background-color: #ffffff;
        color: #000000;
        font-size: 11px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center;
        line-height: initial; }
    &_title {
        margin: 38px 0 0 0;
        font-weight: 700;
        font-size: 38px;
        text-transform: uppercase;
        color: #ffffff;
        text-align: center; }
    &_subtitle {
        margin-bottom: 30px;
        font-weight: 400;
        font-size: 17px;
        color: #ffffff;
        text-align: center; }
    img {
        margin: 54px auto 0 auto;
        display: block;
        // display: flex
 }        // align-items: center
    &_text {
        text-align: center;
        font-weight: 300;
        font-size: 17px;
        color: #222222; } }
.require {
    position: relative;
    min-height: 906px;
    padding: 65px 0 85px 0;
    &_label {
        @include label; }
    &_title {
        @include title; }
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0px; } }
    &_subtitle {
        font-weight: 400;
        font-size: 22px;
        color: $main_color; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            // display: inline-block
            position: relative;
            font-weight: 300;
            font-size: 17px;
            color: #222222;
            margin-bottom: 5px;
            &::before {
                content: '';
                display: block;
                position: absolute;
                left: -32px; //сумма ширины иконки и отступа
                top: 3px;
                // transform: translateY(-50%)
                // display: inline-block
                // transform: translate(-8px, 4px)
                width: 21px;
                height: 20px;
                background: url(../icons/check_blue.png) center (center / cover) no-repeat; } } }
    &_descr {
        font-weight: 300;
        font-size: 17px;
        color: $main_color; }
    .warning {
        .require_subtitle {
            color: $second_color; }
        .require_list {
            li {
                &:before {
                    background: url(../icons/check_red.png) center (center / cover) no-repeat; } } } } }
.world {
    min-height: 720px;
    position: relative;
    width: 100%;
    padding-top: 46px;
    background: url(../img/bg_world.jpg) center (center / cover) no-repeat;
    &_label {
        margin: 0 auto;
        width: 112px;
        height: 24px;
        padding: 5px 12px;
        background-color: #ffffff;
        color: #000000;
        font-size: 11px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center; }
    &_title {
        margin: 38px 0 0 0;
        font-weight: 700;
        font-size: 38px;
        text-transform: uppercase;
        color: #ffffff;
        text-align: center; }
    &_subtitle {
        margin-bottom: 30px;
        font-weight: 400;
        font-size: 17px;
        color: #ffffff;
        text-align: center; }
    &_link {
        position: absolute;
        bottom: 49px;
        left: 50%;
        transform: translateX(-50%);
        text-decoration: none;
        font-weight: 400;
        font-size: 15px;
        text-transform: uppercase;
        color: $main_color;
        transition: 0.3s all;
        &:hover {
            color: #ffffff; } } }
.footer {
    padding: 14px 23px 39px 23px;
    &_line {
        background-color: $main_color;
        height: 7px;
        width: 299px;
        margin: 0 auto; }
    &_wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-top: 32px; }
    &_wrapper > div {
        width: 33.3%;
        &:nth-child(3) {
            display: flex;
            justify-content: flex-end; } }
    &_social {
        width: 160px;
        display: flex;
        justify-content: space-between;
        &_item {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 4px;
            width: 32px;
            height: 32px;
            border-radius: 100%;
            background-color: #000; }
        a {
            color: #fff;
            transition: 0.5s;
            &:hover {
                color: $main-color; } } }
    &_links {
        width: 390px;
        margin: 0 auto;
        &_main {
            display: flex;
            justify-content: space-between;
            padding: 0 10px;
            a {
                font-weight: 300;
                font-size: 15px;
                text-transform: uppercase;
                color: #070716; } }
        &_sub {
            display: flex;
            justify-content: space-between;
            margin-top: 10px;
            a {
                font-weight: 300;
                font-size: 11px;
                text-transform: uppercase;
                color: #999999; } }

        &_lang {
            display: block;
            margin: 0 auto;
            margin-top: 19px;
            font-weight: 400;
            font-size: 11px;
            text-align: center;
            text-transform: uppercase;
            color: $main_color; } }
    &_mobile {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
