@mixin btn_hover {
    &:hover {
        background-image: none;
        background-color: $main_color; } }

@mixin label {
    margin: 0 auto;
    width: 112px;
    height: 24px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    line-height: initial; }
@mixin title {
    margin: 38px 0 0 0;
    font-weight: 700;
    font-size: 38px;
    text-transform: uppercase;
    color: #222222;
    text-align: center; }
